@media (min-width: 1100px) {
    .container {
        padding-left: 100px;
        padding-right: 100px;
        width: 1100px;
    }
}

.container {
    display: -webkit-flex;
    display: flex;
    min-height: 80%;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto;
}
.click-item {
    background-size: cover;
    background-position: 50%;
    height: 170px;
    width: 170px;
    margin: 8px 25px;
    border: 5px solid #f1f1f1;
    border-radius: 3px;
    -webkit-box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
    -webkit-transition-duration: 0.4s;
    box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
    cursor: pointer;
}
.click-item:after{
    opacity: 0;
    -webkit-transition: all 0.8s;
    transition: all 0.8s
}
.navbar {
    background: #e150d5;
    min-height: 70px;
    position: fixed;
    width: 100%;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: #fff;
    font-size: calc(10px + 14 * (100vw - 320px)/ 800);
    z-index: 99;
    white-space: nowrap;
}
.navbar a {
    text-decoration: none;
    color: #fff;
    font-weight: 900;
}
.navbar li {
    display: inline-block;
    padding: 26px 0;
    width: 33.3%;
    text-align: center;
}
.navbar ul {
    width:100%;
    margin: 0;
    padding: 0;
}
.right{
    float: right;
}
.green {
    color: green;
    font-size: 20;
    -webkit-transition-duration: 0.4s;
}
.green::after {
    opacity: 0;
    -webkit-transition: all 0.8s;
    transition: all 0.8s
}
.black {
    color: black;
    font-size: 20;
    -webkit-transition-duration: 0.4s;
}
.black::after {
    opacity: 0;
    -webkit-transition: all 0.8s;
    transition: all 0.8s
}
.Normal {
    color: white;
    font-size: 20;
    -webkit-transition-duration: 0.4s;
}
.Normal::after {
    opacity: 0;
    -webkit-transition: all 0.8s;
    transition: all 0.8s
}
.header {
    background-color: #2c323c;
    background:darkolivegreen;
    background-repeat: repeat;
    color: #fff;
    min-height: 400px;
    padding: 180px 20px;
    padding-bottom: 140px;
    text-align: center;
}
.footer {
    background: #e150d5;
    height: 100px;
    position: relative;
    color: #fff;
    overflow: auto;
}
